import React, {Component} from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import {Redirect, Route, withRouter} from 'react-router-dom';
import News from "./components/Content/News/News";
import Music from "./components/Content/Music/Music";
import Setting from "./components/Content/Setting/Setting";
import MessagesContainer from "./components/Content/Messages/MessagesContainer";
import UsersContainer from "./components/Content/Users/UsersContainer";
import UserProfContainer from "./components/Content/Profile/UserProf/UserProfContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import Login from "./components/Content/Login/Login";
import {connect} from "react-redux";
import {compose} from "redux";
import {initializeApp} from "./Redux/app-reducer";
import Preloader from "./components/common/Preloader";
import {withLazySuspense} from "./hoc/withLazySuspence";


class App extends Component {
    componentDidMount() {

    this.props.initializeApp();
    }


    render() {
        if(!this.props.initialized){

            return(
                <Preloader/>)
        }
        return (

            <div className='app-wrapper'>
                <HeaderContainer/>
                <Navbar/>

                <div className='content'>
                    <Route exact path='/' render={() => <Redirect to="/profile"/>}/>
                    <Route path='/profile/:userId?' render={() => <UserProfContainer/>}/>
                    <Route path='/messages' render={() => <MessagesContainer/>}/>
                    <Route path='/users' render={withLazySuspense(UsersContainer)}/>
                    <Route path='/news' component={News}/>
                    <Route path='/music' component={Music}/>
                    <Route path='/setting' component={Setting}/>
                    <Route path='/login' component={Login}/>
                    <Route path='*' render={() => <div>ERROR 404</div>}/>
                </div>
            </div>

        )
    }
}

const mapState = (state) => {
    return {
        initialized: state.appReducer.initialized,
    }
}

export default compose(withRouter, connect(mapState, {initializeApp}))(App);
