import React from 'react';
import {Field, reduxForm} from "redux-form";
import {Input} from "../../common/FormControls";
import {required} from "../../validators/validator";
import {connect} from "react-redux";
import {loginThunkCreator} from "../../../Redux/auth-reducer";
import {Redirect} from "react-router-dom";
import css from "../../Header/Header.module.css"


const LoginForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
           <div>
               <Field placeholder={"email"} name={"email"} component={Input} validate={[required]}/>
           </div>
            <div>
                <Field placeholder={"password"} name={"password"} component={Input} validate={[required]}/>
            </div>
            <div>
                <Field type={"checkbox"} name={"rememberMe"} component={"input"}/>remember me
            </div>
            {props.captcha && <img src={props.captcha}/>}
            {props.captcha && <Field name={"captcha"} component={Input} validate={[required]}/>}
            <div>
                <button>LOGIN</button>
            </div>
            {props.error && <div className={css.summaryError}>
                {props.error}
            </div>}
        </form>
    )
}

let LoginReduxForm = reduxForm({form: "login",})(LoginForm);

const Login = (props) => {

    const onSubmit = (formData) => {
        props.loginThunkCreator(formData.email, formData.password, formData.rememberMe, formData.captcha);
    }
    if(props.isAuth) {
       return(<Redirect to={"/profile"}/>)
    }
    return (
        <div>
            <div>Login</div><br/>
            <LoginReduxForm onSubmit={onSubmit} captcha={props.captcha}/>
        </div>
    )
}

const mapProps = (state) => {
    return {
        isAuth: state.authReducer.isAuth,
        captcha: state.authReducer.captcha,
    }

}

export default connect( mapProps, {loginThunkCreator})(Login);