import {AuthCaptchApi, HeaderApi, LoginApi, LogoutApi} from "../api/api";
import {stopSubmit} from "redux-form";


let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    captcha: null,
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case "login":
        case "setCaptcha":
            return {
                ...state,
            ...action.data,
            }

        default: return state;

    }
}

export const setAuth = (id, email, login, isAuth) => {
    return {
        type: "login",
        data: {id, email, login, isAuth}
    }
}

export const setCaptcha = (captcha) => {
    return {
        type: "setCaptcha",
        data: {captcha},
    }
}

export const authThunkCreator = () => {
    return (dispatch) => {
        return  HeaderApi().then(data => {

            if(data.resultCode === 0) {

                let {id, login, email} = data.data;
                dispatch(setAuth(id, email, login, true));

            }

        });
    }
}

export const loginThunkCreator = (email, password, rememberMe, captcha) => {
    return (dispatch) => {
        LoginApi(email, password, rememberMe, captcha).then(data => {

            if(data.resultCode === 0) {

                dispatch(authThunkCreator());

            } else {
                if(data.resultCode === 10) {

                    dispatch(captchaThunkCreator());
                }
                debugger
                let message = data.messages.length > 0 ? data.messages[0] : "Error";
                let action = stopSubmit("login", {_error: message});
                dispatch(action);
            }
        })
    }
}

export const logoutThunkCreator = () => {
    return (dispatch) => {
        LogoutApi().then(data => {

            if(data.resultCode === 0) {

                dispatch(setAuth(null, null, null, false));
            }
        })
    }
}

export const captchaThunkCreator = () => {

    return (dispatch) => {
        AuthCaptchApi().then(response => {
            dispatch(setCaptcha(response.data.url));
        })
    }
}



export default authReducer;