import {createSelector} from "reselect";


export const getUersSelector = (state) => {
    return state.usersPage.users;
}

export const superGetUersSelector = createSelector(getUersSelector, (users) => {
    return users.filter((el) => {return true})
})

export const getPageSizeSelector = (state) => {
    return state.usersPage.pageSize;
}

export const getPageTotalSelector = (state) => {
    return state.usersPage.pageTotal;
}

export const getPageCurrentSelector = (state) => {
    return state.usersPage.pageCurrent;
}

export const getIsPreloadSelector = (state) => {
    return state.usersPage.isPreload;
}

export const getFollowinInProgressSelector = (state) => {
    return state.usersPage.followinInProgress;
}