import React from 'react';
import {Field, reduxForm} from "redux-form";
import {Input, Textarea} from "../../../common/FormControls";
import css from '../../../common/FormControls.module.css'


const RedactForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div><button>save</button></div>
            {props.error && <div className={css.formSummaryError}>
                {props.error}
            </div>
            }
            <div>
                <b>Name</b>
                <Field placeholder="Name" name="fullName" component={Input}/>
            </div>
            <div>
                <b>About me</b>
                <Field  name="aboutMe" component={Textarea}/>
            </div>
            <div>
                <b>Looking for a job</b>
                <Field placeholder="Looking for a job" name="lookingForAJob" component={Input} type="checkbox"/>
            </div>
            <div>
                <b>Job description</b>
                <Field placeholder="Job description" name="lookingForAJobDescription" component={Textarea}/>
            </div>
            <div>
                <b>Contacts: </b>
                {Object.keys(props.profile.contacts)
                    .map(key => {let fname = "contacts." + key;
                    return <div key={key}>
                        <b>{key}</b>
                        <Field name={fname} component={Input} />
                    </div>})}
            </div>
        </form>

    )
}


let RedactProfile = reduxForm({form: "profile",})(RedactForm);

export default RedactProfile;
