

let initialState = {
    userDate: [{name: "John" , id: "1"}, {name: "Ferdinand Porshe" , id: "2"}, {name: "Gerodot" , id: "3"}],
    messageDate: [{message: "WWat's up man?", id: "1"}, {message: "No no no", id: "2"}],
};

const messageReducer = (state = initialState, action) => {
    if(action.type === 'addMessage') {
        let newMessage = {
            message: action.newMessageText,
        }
        let stateCopy = {...state};
        stateCopy.messageDate = [...state.messageDate];
        stateCopy.messageDate.push(newMessage);
        stateCopy.newMessageText = "";
        return  stateCopy;
    }
    return state;
}

export const actionAddMessage = (newMessageText) => {
    return {
        type: "addMessage",
        newMessageText: newMessageText,
    }
}



export default messageReducer;