import React from 'react';
import css from './Profile.module.css';
import PostareaContainer from "./Postarea/PostareaContainer";
import UserProf from "./UserProf/UserProf";
import {saveProfile} from "../../../Redux/profile-reducer";


const Profile = (props) => {
    return (
      <div>
        <UserProf profile={props.profile} status={props.status}
                  changeProfStatusThunkCreator={props.changeProfStatusThunkCreator}
                  authUserID={props.authUserID} isOwner={props.isOwner}
                  saveProfile={props.saveProfile}
                  changeProfPhotoThunkCreator={props.changeProfPhotoThunkCreator}
                  />
        <PostareaContainer />

        
      </div>
    )
}

export default Profile;