import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: "https://social-network.samuraijs.com/api/1.0/",
    headers: {"API-KEY": "c16aff2d-866d-4745-8137-5ce0be98a841"},
})

export const ProfileApi = (userId) => {
    return instance.get(`profile/` + userId).then(response => {
        return response.data;
    });
}

export const ProfileGetStatusApi = (userId) => {
    return instance.get(`profile/status/${userId}`).then(response => {
        return response.data;
    });
}

export const ProfileChangeStatusApi = (status) => {
    return instance.put(`profile/status/`, {status: status}).then(response => {
        return response.data;
    });
}

export const SaveProfilePhotoApi = (photos) => {
    const formData = new FormData();
    formData.append("image", photos);
    return instance.put(`profile/photo`, formData, {
        headers: {
            'Content-type': 'multipart/form-data',
        }
    }).then(response => {
        return response.data;
    })
}

export const SaveProfileApi = (profile) => {
    return instance.put(`profile`, profile);
}

export const HeaderApi = (userId) => {
    return instance.get(`auth/me`).then(response => {
        return response.data;
    });
}

export const LoginApi = (email, password, rememberMe = false, captcha = null) => {
    return instance.post(`/auth/login`, {email, password, rememberMe, captcha}).then(response => {
        return response.data;
    })
}

export const LogoutApi = () => {
    return instance.delete(`/auth/login`).then(response => {
        return response.data;
    })
}

export const AuthCaptchApi = () => {
    return instance.get(`security/get-captcha-url`);
}

export const UsersUnfollowApi = (id) => {
    return instance.delete(`follow/${id}`).then(response => {
        return response.data;
    });
}

export const UsersFollowApi = (id) => {
    return instance.post(`follow/${id}`, {}).then(response => {
        return response.data;
    });
}

export const UsersPageApi = (arg1, arg2) => {
    return instance.get(`users?page=${arg1}&count=${arg2}`).then(response => {
        return response.data;
    });
}

