import React from 'react';
import {actionAddMessage, actionUpdateMessage} from "../../../Redux/message-reducer";
import Messages from "./Messages";
import {connect} from "react-redux";
import {withAuthRedirect} from "../../../hoc/withAuthRedirect";
import {compose} from "redux";





let mapProps = (state) => {
    return {
        mDate: state.messagePage.messageDate,
        uDate: state.messagePage.userDate,
    }
}

let mapMethods = (dispatch) => {
    return {
        AddMessage: (newMessageText) => {dispatch(actionAddMessage(newMessageText));},
    }
}



export default compose(connect(mapProps, mapMethods), withAuthRedirect)(Messages);
