import React from 'react';
import css from './Users.module.css';
import {NavLink} from "react-router-dom";
import Paginator from "../../common/Paginator";
import User from "./User/User";

const Users = (props) => {


    return (

        <div>
            <Paginator className={css.paginator} pageTotal={props.pageTotal}
                       pageSize={props.pageSize}
                       pageCurrent={props.pageCurrent}
                       pageChange={props.pageChange}/>
            {
                props.users.map((el) =>

                    <div key={el.id}>

                        <User el={el}  followinInProgress={props.followinInProgress}
                              unfollowUsersThunkCreator={props.unfollowUsersThunkCreator}
                              followUsersThunkCreator={props.followUsersThunkCreator}/>
                    </div>
                )
            }

        </div>
    )
}

export default Users;