import {UsersFollowApi, UsersPageApi, UsersUnfollowApi} from "../api/api";

let initialState = {
    users: [],
    pageSize: 5,
    pageTotal: 0,
    pageCurrent: 1,
    isPreload: true,
    followinInProgress: [],
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case "follow":
            return {
                ...state,
                users: state.users.map((el) => {
                    if (el.id === action.userID) {
                        return {...el, followed: true};
                    }
                    return el;
                })
            }
        case "unfollow":
            return {
                ...state,
                users: state.users.map((el) => {
                    if (el.id === action.userID) {
                        return {...el, followed: false};
                    }
                    return el;
                })
            }
        case "SET_USERS": {
            return {...state, users: action.users}
        }
        case "SET_USERS_COUNT": {
            return {...state, pageTotal: action.totalCount}
        }
        case "SET_CURRENT_PAGE": {
            return {...state, pageCurrent: action.current}
        }
        case "TOGGLE_PRELOADER": {
            return {...state, isPreload: action.isFetching}
        }
        case "FOLLOWING_PROGRESS": {
            return {...state, followinInProgress: action.progress
                    ? [...state.followinInProgress, action.userId]
                    : state.followinInProgress.filter(id=>id != action.userId)}
        }
        default: return state;

    }
}

export const Follow = (userID) => {
    return {
        type: "follow",
        userID: userID,
    }
}

export const Unfollow = (userID) => {
    return {
        type: "unfollow",
        userID: userID,
    }
}

export const setUsers = (users) => {
    return {
        type: "SET_USERS",
        users: users,
    }
}

export const setTotalCount = (totalCount) => {
    return {
        type: "SET_USERS_COUNT",
        totalCount: totalCount,
    }
}

export const setCurrentPage = (current) => {
    return {
        type: "SET_CURRENT_PAGE",
        current: current,
    }
}

export const togglePreloader = (isFetching) => {
    return {
        type: "TOGGLE_PRELOADER",
        isFetching: isFetching,
    }
}

export const FollowingProgress = (progress, userId) => {
    return {
        type: "FOLLOWING_PROGRESS",
        progress: progress,
        userId: userId,
    }
}

export const getUsersThunkCreator = (pageCurrent, pageSize) => {

    return (dispatch) => {
        dispatch(togglePreloader(true));
        UsersPageApi(pageCurrent, pageSize).then(data => {
            dispatch(setUsers(data.items));
            dispatch(setTotalCount(data.totalCount));
            dispatch(togglePreloader(false));
        });
    }

}

export const unfollowUsersThunkCreator = (id) => {
    return (dispatch) => {
        dispatch(FollowingProgress(true, id));
        UsersUnfollowApi(id).then(data => {

            if(data.resultCode === 0) {
                dispatch(Unfollow(id))
            }
            dispatch(FollowingProgress(false, id));
        });
    }
}

export const followUsersThunkCreator = (id) => {
    return (dispatch) => {
        dispatch(FollowingProgress(true, id));
        UsersFollowApi(id).then(data => {

            if(data.resultCode === 0) {
                dispatch(Follow(id))
            }
            dispatch(FollowingProgress(false, id));
        });
    }
}

export default usersReducer;