import React from "react";
import {NavLink} from "react-router-dom";
import css from "../Users.module.css";

const User = (props) => {
    let el = props.el;
    return (
        <div>
            <NavLink to={"/profile/" + el.id}><img src={el.photos.small != null
                ? el.photos.small : "http://icons.iconarchive.com/icons/papirus-team/papirus-status/512/avatar-default-icon.png"}
                                                   className={css.image}/></NavLink>
            <div>{el.name}</div>
            <div>{el.status}</div>
            {/*<div>{el.location.city}</div>*/}
            {/*<div>{el.location.country}</div>*/}
            <div>
                {el.followed ? <button disabled={props.followinInProgress.some(id=>id===el.id)} onClick={() => {
                        props.unfollowUsersThunkCreator(el.id);
                    }}>Unfollow</button>
                    : <button disabled={props.followinInProgress.some(id=>id===el.id)} onClick={() => {
                        props.followUsersThunkCreator(el.id);

                    }}>Follow</button>}
            </div>
        </div>

    )
}

export default User;
