import {ProfileApi, ProfileChangeStatusApi, ProfileGetStatusApi, SaveProfileApi, SaveProfilePhotoApi} from "../api/api";
import {stopSubmit} from "redux-form";


let initialState = {
    postDate: [{text: "Somebody ones told me world is gonna roll me ...", id: "1"}, {text: "cho", id: "2"}, {text: "kavo", id: "3"}],
    profile: null,
    status: "gekkoo",
};

const profileReducer = (state = initialState, action) => {
    if(action.type === 'addPost') {
        let newPost = {
            text: action.newPostText,
        }
        let stateCopy = {...state};
        stateCopy.postDate = [...state.postDate];
        stateCopy.postDate.unshift(newPost);
        return stateCopy;
    } else if(action.type === 'updateProfile') {
        return {...state, profile: action.profile}
    } else if(action.type === "setStatus") {
        return {...state, status: action.string}
    } else if(action.type === "changeStatus") {
        return  {...state, status: action.status }
    } else if(action.type === "deletePost") {
        return  {...state, postDate: state.postDate.filter(p => p.id != action.id) }
    } else if(action.type === "changePhotoProf") {
        return  {...state, profile: {...state.profile, photos: action.photos}  }
    }
    return state;
}

export const actionAddPost = (newPostText) => {
    return {
        type: "addPost",
        newPostText: newPostText,
    }
}


export const actionDeletePost = (id) => {
    return {
        type: "deletePost",
        id: id,
    }
}

export const setUserProfile = (profile) => {
    return {
        type: "updateProfile",
        profile: profile,
    }
}

export const setStatusProfile = (string) => {
    return {
        type: "setStatus",
        string: string,
    }
}


export const changeStatusProfile = (status) => {
    return {
        type: "changeStatus",
        status: status,
    }
}

export const changePhotoProfile = (photos) => {
    return {
        type: "changePhotoProf",
        photos: photos,
    }
}



export const addPostThunkCreator = (userId) => {
    return (dispatch) => {


        ProfileApi(userId).then(data => {
            dispatch(setUserProfile(data));
        });
    }
}

export const addProfStatusThunkCreator = (userId) => {
    return (dispatch) => {

        if(!userId){
            userId = 1793;
        }

        ProfileGetStatusApi(userId).then(data => {
            dispatch(setStatusProfile(data));
        });
    }
}

export const changeProfStatusThunkCreator = (status) => {
    return (dispatch) => {

        ProfileChangeStatusApi(status).then(data => {
            dispatch(changeStatusProfile(status));
        });
    }
}

export const changeProfPhotoThunkCreator = (photos) => {
    return (dispatch) => {

        SaveProfilePhotoApi(photos).then(data => {
            dispatch(changePhotoProfile(data.photos));
        });
    }
}



export const saveProfile = (profile) => async (dispatch, getState) => {
    const userId = getState().authReducer.id;
    const response = await SaveProfileApi(profile);
    if(response.data.resultCode === 0) {
        dispatch(addPostThunkCreator(userId));
    } else {
        dispatch(stopSubmit("profile", {_error: response.data.messages[0] }));
        return Promise.reject(response.data.messages[0]);
    }
}

export default profileReducer;