import Postarea from "./Postarea";
import React from "react";
import {actionAddPost, actionUpdatePost} from "../../../../Redux/profile-reducer";
import {connect} from "react-redux";




let MapProps = (state) => {
   return {
       posts: state.profilePage.postDate,

   }
};

let MapMeth = (dispatch) => {
    return {
        AddPost: (newPostText) => {
            dispatch(actionAddPost(newPostText));
        },

    }
};

const SuperPostareaContainer = connect(MapProps, MapMeth) (Postarea);

export default SuperPostareaContainer;