import React from 'react';
import css from './Messages.module.css';
import {NavLink, Redirect} from "react-router-dom";
import handleSubmit from "redux-form/lib/handleSubmit";
import {Field, reduxForm} from "redux-form";

const User = (props) => {
    let path = "/dialogs/" + props.id;
    return (
        <div>
            <NavLink to={path}>{props.name}</NavLink>
        </div>
    )
}

const Chat = (props) => {
    return (
        <div>
            {props.message}
        </div>
    )
}


const Messages = (props) => {


    let userElem = props.uDate.map((el) => {
        return (<User name={el.name} key={el.id} id={el.id}/>)
    });
    let messageElem = props.mDate.map((el) => {
        return (<Chat message={el.message} key={el.id}/>)
    });


    let clickFun = (values) => {
        props.AddMessage(values.newMessageText);
    }


    return (
        <div className={css.messages}>
            <div className={css.Users}>
                {userElem}
            </div>
            <div className={css.chats}>
                {messageElem}
            </div>

            <AddMessageReduxForm onSubmit={clickFun}/>

        </div>
    )
}

const AddMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field  name={"newMessageText"} component={"textarea"}/>
            </div>
            <div>
                <button >SEND MESSAGE</button>
            </div>
        </form>
    )
}

const AddMessageReduxForm = reduxForm({form: "addMessageForm"})(AddMessageForm);

export default Messages;

