import React from 'react';
import Post from './Post/Post'
import css from './Postarea.module.css';
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../validators/validator";
import {Textarea} from "../../../common/FormControls";


const Postarea = React.memo((props) => {

    let postElement = props.posts.map((el) => {
        return (<Post text={el.text} key={el.id}/>)
    });

    let start = (values) => {
        props.AddPost(values.newPostText);
    }

    return (
        <div>
            <AddPostReduxForm onSubmit={start}/>
            <div className={css.postarea}>
                {postElement}
            </div>
        </div>
    )
})


let permitLenght = maxLengthCreator(20);

const AddPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} name={"newPostText"} validate={[required, permitLenght]} placeholder={"What are you want to add?"}/>
            </div>
            <div>
                <button>ADD POST</button>
            </div>

        </form>
)
}

const AddPostReduxForm = reduxForm({form: "addPostForm"})(AddPostForm);



export default Postarea;