import React, {useState} from 'react';
import css from "../Profile.module.css";
import Preloader from "../../../common/Preloader";
import ProfileStatusHooks from "./ProfileStatusHooks";
import RedactProfile from "./RedactProfile";


const UserProf = (props) => {

    let[editMode, setEditMode] = useState(false);

    if(!props.profile){
        return <Preloader/>
    }

    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.changeProfPhotoThunkCreator(e.target.files[0]);
        }
    }

    const onSubmit = (formData) => {
        props.saveProfile(formData).then(() => {
            setEditMode(false);
        })

    }

    return (
        <div>
            <div><img src='http://www.kartinki24.ru/uploads/gallery/main/2/kartinki24_ru_abstract_88.jpg'
                      className={css.dark}></img></div>
            <div className={css.ava}>
                <img src={props.profile.photos.large ||
                "http://icons.iconarchive.com/icons/papirus-team/papirus-status/512/avatar-default-icon.png"} className={css.mainPhoto}/>
                {props.isOwner && <input type={"file"} onChange={onMainPhotoSelected}/>}

                <ProfileStatusHooks status={props.status} changeProfStatusThunkCreator={props.changeProfStatusThunkCreator}/>

                {editMode ? <RedactProfile initialValues={props.profile} onSubmit={onSubmit} profile={props.profile}/>:  <ProfileProperties profile={props.profile}
                                                                  goToEditMode={() => {setEditMode(true)}}
                                                                  isOwner={props.isOwner}/>}


            </div>
        </div>
    )
}

const Contact = (props) => {
    return (
        <div>
            <b>{props.title}</b>:  {props.value ? props.value: "none"}
        </div>
    )
}

const ProfileProperties = (props) => {
    return (
        <div>
            {props.isOwner && <div><button onClick={props.goToEditMode}>Edit</button></div>}
            <div>
                Name: {props.profile.fullName}
            </div>
            <div>
                About me: {props.profile.aboutMe}
            </div>
            <div>
                Looking for a job: {props.profile.lookingForAJob ? "YES":"NO"}
            </div>
            <div>
                Job description: {props.profile.lookingForAJobDescription}
            </div>
            <div>{Object.keys(props.profile.contacts)
                .map(key => {return <Contact key={key} title={key} value={props.profile.contacts[key]}/>})}</div>
        </div>
    )
}

export default UserProf;