import React from 'react';
import preloader from "../../images/91.gif";
import css from "./Preloader.module.css";

let Preloader = (props) => {
    return (
        <div className={css.preloader}>
            <div  style={ { backgroundColor: 'white' } }>
            <img src={preloader} />
        </div>
        </div>)
}

export default Preloader;