import React from 'react';
import {connect} from "react-redux";
import {
    addPostThunkCreator,
    addProfStatusThunkCreator, changeProfPhotoThunkCreator,
    changeProfStatusThunkCreator, saveProfile,
} from "../../../../Redux/profile-reducer";
import {withRouter} from "react-router-dom";
import Profile from "../Profile";
import {withAuthRedirect} from "../../../../hoc/withAuthRedirect";
import {compose} from "redux";

class UserProfComponent extends React.Component {

    refreshProfile() {

        let userId = this.props.match.params.userId;
        if(!userId){
            userId = this.props.authUserID;
            if(!userId){
                this.props.history.push("/login")

            }
        }

        this.props.addPostThunkCreator(userId);
        this.props.addProfStatusThunkCreator(userId);
    }

    componentDidMount() {
        this.refreshProfile();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.userId != prevProps.match.params.userId ) {
            this.refreshProfile();
        }
    }

    render() {

        return(
            <div>
                <Profile {...this.props} profile={this.props.profile} status={this.props.status}
                         changeProfStatusThunkCreator={this.props.changeProfStatusThunkCreator}
                         authUserID={this.props.authUserID} isOwner={!this.props.match.params.userId}
                         saveProfile={this.props.saveProfile}
                         changeProfPhotoThunkCreator={this.props.changeProfPhotoThunkCreator}
                         />
            </div>
        )
    }
}

let disProps = (state) => {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        isAuth: state.authReducer.isAuth,
        authUserID: state.authReducer.id,
    }
}


export default compose(connect(disProps,{addPostThunkCreator, saveProfile, changeProfPhotoThunkCreator, addProfStatusThunkCreator, changeProfStatusThunkCreator}),
                        withRouter, withAuthRedirect)(UserProfComponent);