import React from 'react';
import {connect} from "react-redux";
import Users from "./Users";
import css from "./UsersContainer.module.css";
import Preloader from "../../common/Preloader";
import {followUsersThunkCreator, unfollowUsersThunkCreator} from "../../../Redux/users-reducer";
import {setCurrentPage} from "../../../Redux/users-reducer";
import {getUsersThunkCreator} from "../../../Redux/users-reducer";
import {
    getFollowinInProgressSelector,
    getIsPreloadSelector,
    getPageCurrentSelector,
    getPageSizeSelector,
    getPageTotalSelector, superGetUersSelector,

} from "../../../Redux/selectors/users-selectors";

class UsersComponent extends React.Component {

    componentDidMount() {

        this.props.getUsersThunkCreator(this.props.pageCurrent, this.props.pageSize);

    }

    pageChange = (pageNumber) => {
        this.props.setCurrentPage(pageNumber);
        this.props.getUsersThunkCreator(pageNumber, this.props.pageSize);

    }

    render() {

        return (

            <div>
                {(this.props.isPreload) ? <div className={css.preloader}><Preloader/></div>: null}

                <Users pageCurrent={this.props.pageCurrent}
                       pageChange={this.pageChange}
                       users={this.props.users}
                       pageTotal={this.props.pageTotal}
                       pageSize={this.props.pageSize}
                       followinInProgress={this.props.followinInProgress}
                       unfollowUsersThunkCreator={this.props.unfollowUsersThunkCreator}
                       followUsersThunkCreator={this.props.followUsersThunkCreator}/>
            </div>
        )
    }
}

let MapProps = (state) => {

    return {
        users: superGetUersSelector(state),
        pageSize: getPageSizeSelector(state),
        pageTotal: getPageTotalSelector(state),
        pageCurrent: getPageCurrentSelector(state),
        isPreload: getIsPreloadSelector(state),
        followinInProgress: getFollowinInProgressSelector(state),
    }

}

